import  React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';


class Projects extends Component {
    constructor(props) {
        super(props)
        this.state = { activeTab: 0};
    }

     toggleCategories() {

      if(this.state.activeTab === 0)  {
          return(
              <div  className="projects-grid">
       {/* Projects1*/}
     <Card shadow={0} style={{width: '320px', margin: 'auto'}}>
    <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,) center / cover'}}>React projects1</CardTitle>
    <CardText>
    The single greatest invention man ever conceived in the dollar bill,
    because I don't want to know the conversion rate for coconuts.
        
    </CardText>
    <CardActions border>
        <Button colored>Github</Button>
        <Button colored>Facebook</Button>
        <Button colored>Twitter</Button>
    </CardActions>
    <CardMenu style={{color: '#fff'}}>
        <IconButton name="share" />
    </CardMenu>
   </Card>

     {/* Projects2*/}
     <Card shadow={0} style={{width: '320px', margin: 'auto'}}>
    <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,) center / cover'}}>React projects2</CardTitle>
    <CardText>
    The Best way to help the world is to help someone!
    “Most folks are about as happy as they make their minds up to be.” 
    “No man has a good enough memory to be a successful liar.”  
    </CardText>
    <CardActions border>
        <Button colored>Github</Button>
        <Button colored>Facebook</Button>
        <Button colored>Twitter</Button>
    </CardActions>
    <CardMenu style={{color: '#fff'}}>
        <IconButton name="share" />
    </CardMenu>
    </Card>

          {/* Projects3*/}
     <Card shadow={0} style={{width: '320px', margin: 'auto'}}>
    <CardTitle style={{color: '#fff', height: '176px', background: 'url(data:image/jpeg;base64,) center / cover'}}>React projects3</CardTitle>
    <CardText>
    Perhaps you will have to spend hours on your knees or upon your face before the throne. 
    Never mind. Wait. God will do great things for you if you will wait for Him.
    Yield to Him. Cooperate with Him.
    </CardText>
    <CardActions border>
        <Button colored>Github</Button>
        <Button colored>Facebook</Button>
        <Button colored>Twitter</Button>
    </CardActions>
    <CardMenu style={{color: '#fff'}}>
        <IconButton name="share" />
    </CardMenu>
    </Card>

     </div>
         
 

          );
      } else if(this.state.activeTab ===1){
          return(
            <div><h1>this is Html</h1></div>
          );
      }else if(this.state.activeTab ===2){
          return(
              <div><h1>this is Css</h1></div>
          );
      }else if(this.state.activeTab===3){
          return(
              <div><h1>this is Javascript</h1></div>
          );
      }
     }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>React</Tab>
                    <Tab>Html</Tab>
                    <Tab>Css</Tab>
                    <Tab>Javascript</Tab>
                </Tabs>
                <section>
                <Grid>
                 <Cell col={12}>
                 <div className="content">{this.toggleCategories()}</div>
                 </Cell>
                </Grid>
                </section>
            </div>    
        );
    }
}
   










export default Projects;
